html, body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans Condensed', sans-serif;
}

html {
  font-size: 10px;
  background-color: #f0f0f0;
}

body {
  width: 80%;
  min-width: 1024px;
  margin: 0 auto;
}

/* Typography */
h1, h2, h3 {
  font-family: 'Sonsie One', cursive;
  color: #333333;
}

h1 {
  font-size: 4rem;
  text-align: center;
  color: #ffffff;
  text-shadow: 2px 2px 10px #000000;
}

h2 {
  font-size: 3rem;
  text-align: center;
  color: #333333;
}

h3 {
  font-size: 2.2rem;
  color: #333333;
}

input, li, table, label {
  font-size: 1.6rem;
  line-height: 1.5;
  color: #333333;
}

p {
  font-size: 1.8rem; /* Increase font size */
  line-height: 1.8;  /* Increase line height */
  padding: 10px 0;   /* Add padding for better spacing */
}

/* Navigation */
nav {
  background-color: #009688;
  display: flex;
  padding: 1% 0;
  margin-bottom: 10px;
}

nav ul {
  flex: 2;
  display: flex;
  padding: 0;
  list-style-type: none;
}

nav li {
  flex: 1;
  text-align: center;
}

nav a {
  font-size: 2rem;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
}

/* Search form */
.search {
  flex: 1;
  display: flex;
  align-items: center;
}

.search input[type="search"] {
  flex: 3;
  font-size: 1.6rem;
  height: 32px;
}

.search input[type="submit"] {
  flex: 1;
  margin-left: 1rem;
  background: #00796b;
  border: none;
  color: #ffffff;
}

/* Main Layout */
main {
  display: flex;
}

article {
  flex: 5;
  padding: 10px 30px;
  background-color: #00796b;
}

.secondary {
  flex: 2;
  background-color: #00796b;
  padding: 10px 30px;
}

footer {
  margin-top: 10px;
  padding: 10px 30px;
  background-color: #00796b;
  text-align: center;
  color: #ffffff;
}

/* Table Styling */
table {
  width: 100%;
  background-color: #ffffff;
  border-collapse: collapse;
}

th, td {
  padding: 10px;
}

th {
  border-bottom: 1px solid #666666;
}

tbody tr:nth-child(odd) {
  background-color: #e0f2f1;
}

/* Styling for images and audio */
img, audio {
  display: block;
  margin: 0 auto;
}

audio {
  width: 500px;
}

/* Comments Section */
.comments {
  background-color: #e0f2f1;
  padding: 10px;
}

.show-hide {
  cursor: pointer;
  text-align: center;
  font-size: 1.6rem;
  background: #00796b;
  color: #ffffff;
  width: 150px;
  margin: 10px auto;
}

.comment-form {
  margin-bottom: 3rem;
}

.comment-form .flex-pair {
  display: flex;
  padding: 0 3rem 1rem;
}

.comment-form label {
  align-self: center;
  flex: 2;
  text-align: right;
}

.comment-form input {
  margin-left: 1rem;
  flex: 6;
}

.comment-form input[type="submit"] {
  width: 30%;
  display: block;
  margin: 0 auto;
  background: #00796b;
  border: none;
  color: #ffffff;
}

.comment-container {
  margin-top: 0;
}

.comment-container li {
  list-style-type: none;
  display: flex;
}

.comment-container li p:first-of-type {
  flex: 1;
  font-weight: bold;
}

.comment-container li p:nth-of-type(2) {
  flex: 5;
}

/* Visually Hidden */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.visually-hidden-table {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA,kBAAkB;AAClB;EACE,SAAS;EACT,UAAU;EACV,8CAA8C;AAChD;;AAEA;EACE,eAAe;EACf,yBAAyB;AAC3B;;AAEA;EACE,UAAU;EACV,iBAAiB;EACjB,cAAc;AAChB;;AAEA,eAAe;AACf;EACE,kCAAkC;EAClC,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,kBAAkB;EAClB,cAAc;EACd,iCAAiC;AACnC;;AAEA;EACE,eAAe;EACf,kBAAkB;EAClB,cAAc;AAChB;;AAEA;EACE,iBAAiB;EACjB,cAAc;AAChB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,iBAAiB,EAAE,uBAAuB;EAC1C,gBAAgB,GAAG,yBAAyB;EAC5C,eAAe,IAAI,mCAAmC;AACxD;;AAEA,eAAe;AACf;EACE,yBAAyB;EACzB,aAAa;EACb,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,aAAa;EACb,UAAU;EACV,qBAAqB;AACvB;;AAEA;EACE,OAAO;EACP,kBAAkB;AACpB;;AAEA;EACE,eAAe;EACf,yBAAyB;EACzB,qBAAqB;EACrB,cAAc;AAChB;;AAEA,gBAAgB;AAChB;EACE,OAAO;EACP,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,iBAAiB;EACjB,YAAY;AACd;;AAEA;EACE,OAAO;EACP,iBAAiB;EACjB,mBAAmB;EACnB,YAAY;EACZ,cAAc;AAChB;;AAEA,gBAAgB;AAChB;EACE,aAAa;AACf;;AAEA;EACE,OAAO;EACP,kBAAkB;EAClB,yBAAyB;AAC3B;;AAEA;EACE,OAAO;EACP,yBAAyB;EACzB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,yBAAyB;EACzB,kBAAkB;EAClB,cAAc;AAChB;;AAEA,kBAAkB;AAClB;EACE,WAAW;EACX,yBAAyB;EACzB,yBAAyB;AAC3B;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,yBAAyB;AAC3B;;AAEA,iCAAiC;AACjC;EACE,cAAc;EACd,cAAc;AAChB;;AAEA;EACE,YAAY;AACd;;AAEA,qBAAqB;AACrB;EACE,yBAAyB;EACzB,aAAa;AACf;;AAEA;EACE,eAAe;EACf,kBAAkB;EAClB,iBAAiB;EACjB,mBAAmB;EACnB,cAAc;EACd,YAAY;EACZ,iBAAiB;AACnB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,oBAAoB;AACtB;;AAEA;EACE,kBAAkB;EAClB,OAAO;EACP,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;EACjB,OAAO;AACT;;AAEA;EACE,UAAU;EACV,cAAc;EACd,cAAc;EACd,mBAAmB;EACnB,YAAY;EACZ,cAAc;AAChB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,qBAAqB;EACrB,aAAa;AACf;;AAEA;EACE,OAAO;EACP,iBAAiB;AACnB;;AAEA;EACE,OAAO;AACT;;AAEA,oBAAoB;AACpB;EACE,kBAAkB;EAClB,UAAU;EACV,WAAW;EACX,YAAY;EACZ,UAAU;EACV,gBAAgB;EAChB,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,WAAW;EACX,YAAY;EACZ,UAAU;EACV,gBAAgB;EAChB,sBAAsB;EACtB,SAAS;AACX","sourcesCon